import { mapGetters, mapActions } from 'vuex'
import { goToBookDetail, appendAddToShelf, removeAddToShelf } from './store.js'
import { themeList, addCss, removeAllCss, getReadTimeByMinutes } from './book.js'
import { saveLocation, getBookmark, saveBookShelf, getBookShelf } from './localStorage.js'
import { shelf } from '../api/store.js'
export const ebookMixin = {
  computed: {
    ...mapGetters([
      'fileName', // 
      'menuVisible', // 
      'settingVisible', // -1:, 0:, 1:, 2:, 3:
      'defaultFontSize', // 
      'defaultFontFamily', // 
      'fontFamilyVisible', // 
      'defaultTheme', // 
      'bookAvailable', // 
      'progress', // 
      'section', // 
      'isPaginating',
      'currentBook', // iframe
      'navigation', // 
      'cover', // 
      'metadata', // 
      'paginate',
      'pageList',
      'offsetY', // 
      'isBookmark' // 
    ]),
    themeList() {
      return themeList(this)
    },
    // 
    getSectionName() {
      if (this.navigation) {
        return this.navigation[this.section].label
      }
      return ''
    }
  },
  methods: {
    ...mapActions([
      'setFileName',
      'setMenuVisible',
      'setSettingVisible',
      'setDefaultFontSize',
      'setDefaultFontFamily',
      'setFontFamilyVisible',
      'setDefaultTheme',
      'setBookAvailable',
      'setProgress',
      'setSection',
      'setIsPaginating',
      'setCurrentBook',
      'setNavigation',
      'setCover',
      'setMetadata',
      'setPaginate',
      'setPageList',
      'setOffsetY',
      'setisBookmark'
    ]),
    // 
    initClobalStyle() {
      removeAllCss()
      switch (this.defaultTheme) {
        case 'Default':
          addCss(`${process.env.VUE_APP_RES_URL}/theme/theme_default.css`)
          break
        case 'Gold':
          addCss(`${process.env.VUE_APP_RES_URL}/theme/theme_gold.css`)
          break
        case 'Eye':
          addCss(`${process.env.VUE_APP_RES_URL}/theme/theme_eye.css`)
          break
        case 'Night':
          addCss(`${process.env.VUE_APP_RES_URL}/theme/theme_night.css`)
          break
        default:
          addCss(`${process.env.VUE_APP_RES_URL}/theme/theme_default.css`)
          break
      }
    },
    // 
    refreshLocation() {
      // 
      const currentLocation = this.currentBook.rendition.currentLocation()
      if (currentLocation.start) {
        const startCfi = currentLocation.start.cfi
        const progress = this.currentBook.locations.percentageFromCfi(startCfi)
        // 
        this.setProgress(Math.floor(progress * 100))
        // 
        this.setSection(currentLocation.start.index)
        // 
        saveLocation(this.fileName, startCfi)
        // 
        const bookmark = getBookmark(this.fileName)
        if (bookmark) {
          if (bookmark.some(item => item.cfi === startCfi)) {
            this.setisBookmark(true)
          } else {
            this.setisBookmark(false)
          }
        } else {
          this.setisBookmark(false)
        }
      }
    },
    // 
    toggleTitleAndMenu() {
      this.setMenuVisible(false)
    },
    // 
    showOrHideMenu() {
      this.setMenuVisible(!this.menuVisible)
      if (this.settingVisible !== -1) {
        this.setSettingVisible(-1)
      }
      if (this.fontFamilyVisible) {
        this.setFontFamilyVisible(!this.fontFamilyVisible)
      }
    },
    // 
    getReadTimeText(fileName) {
      return this.$t('book.haveRead').replace('$1', getReadTimeByMinutes(fileName))
    },
    // 
    display(href, cb) {
      if (href) {
        this.currentBook.rendition.display(href).then(() => {
          this.refreshLocation()
          if (cb) cb()
        })
      } else {
        this.currentBook.rendition.display().then(() => {
          this.refreshLocation()
          if (cb) cb()
        })
      }
    }
  }
}

export const storeHomeMixin = {
  computed: {
    ...mapGetters([
      'offsetY',
      'hotSearchOffsetY',
      'flapCardVisible',
      'search',
      'isEditMode'
    ])
  },
  methods: {
    ...mapActions([
      'setOffsetY',
      'setHotSearchOffsetY',
      'setFlapCardVisible',
      'setSearch'
      ]),
      showBookDetail(book) {
        goToBookDetail(this, book)
      }
  }
}

export const storeShelfMixin = {
  computed: {
    ...mapGetters([
      'isEditMode', // 
      'shelfList', // 
      'shelfSelected', // 
      'shelfTitleVisible', // 
      'offsetY',
      'shelfCategory', // 
      'currentType' // 1 2
    ])
  },
  methods: {
    ...mapActions([
      'setIsEditMode',
      'setShelfList',
      'setShelfSelected',
      'setShelfTitleVisible',
      'setOffsetY',
      'setShelfCategory',
      'setCurrentType'
    ]),
    showBookDetail(book) {
      goToBookDetail(this, book)
    },
    getShelfList() {
      let shelfList = getBookShelf()
      if (!shelfList) {
        shelf().then(response => {
          if (response.status === 200 && response.data && response.data.bookList) {
            shelfList = appendAddToShelf(response.data.bookList)
            saveBookShelf(shelfList)
            return this.setShelfList(shelfList)
          }
        })
      } else {
        return this.setShelfList(shelfList)
      }
    },
    getCategoryList(title) {
      this.getShelfList().then(() => {
        this.setShelfCategory(this.shelfList.filter(item => item.title === title && item.type === 2)[0])
      })
    },
    moveOutFromGroup(group, c) {
      this.setShelfList(this.shelfList.filter(book => {
        if (book.type === 2 && book.itemList) {
          book.itemList = book.itemList.filter((subBook) => group.indexOf(subBook) < 0)
        }
        return group.indexOf(book) < 0
      })).then(() => {
        this.addToShelf(group, c)
      })
    },
    addToShelf(group, c) {
      let shelfList = []
      if (this.shelfList) {
        shelfList = removeAddToShelf(this.shelfList)
      }
      shelfList = [].concat(shelfList, ...group)
      shelfList.forEach((item, index) => {
        item.id = index + 1
      })
      this.setShelfList(appendAddToShelf(shelfList))
      if (c) c()
    }
  }
}
